<template>
    <div class="forgotpass-container">
        <v-dialog v-model="resetPasswordModal" max-width="320px">
            <v-card>
                <v-card-title>Reset Password</v-card-title>

                <v-card-text class="delete-address-container" v-if="!resetPasswordFail.visible && !resetPasswordSuccess.visible">
                    <v-container grid-list-md>
                        <v-layout wrap>
                            Are you sure you want delete this address?
                        </v-layout>
                    </v-container>
                </v-card-text>

                <v-card-text v-if="resetPasswordFail.visible" class="addaddress-result-container">
                    <v-flex class="deladdress-result-img">
                        <v-img src="../assets/unsuccessful.gif"></v-img>
                    </v-flex>
                    <v-text class="deladdress-fail-text">{{resetPasswordFail.text}}</v-text>
                </v-card-text>

                <v-card-text v-if="resetPasswordSuccess.visible" class="addaddress-result-container">
                    <v-flex class="deladdress-result-img">
                        <v-img src="../assets/successful.gif"></v-img>
                    </v-flex>
                    <v-text class="deladdress-success-text">{{resetPasswordSuccess.text}}</v-text>
                </v-card-text>

                <v-card-actions>
                    <v-btn color="blue darken-1" class="close-change-pass-modal" text v-on:click="resetPasswordModal = false">Close</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>


        <v-flex xs4 offset-xs4 class="forgotpass-form" v-if="!resetPasswordSuccess.visible && !resetPasswordFail.visible">
            <div class="forgotpass-title">Forgot Password</div>
            <div class="forgotpass-subtitle">Enter the email address you used to sign up and we'll<br>send you a new password.</div>

            <v-form ref="form" v-model="valid">
                <float-label>
                    <v-text-field type="text" placeholder="Email" v-model='resetPasswordEmail' :rules="[rules.required]" auto-capitalize="none"></v-text-field>
                </float-label>
                <v-flex>
                    <v-btn class="submit resetpassword-submit" v-on:click="resetPassword" :disabled="!valid">RESET PASSWORD</v-btn>
                </v-flex>
            </v-form>
        </v-flex>

        <v-flex xs4 offset-xs4 class="forgotpass-form forgotpass-result-success" v-if="resetPasswordSuccess.visible">
            <div class="forgotpass-title">Success!</div>
            <div class="forgotpass-presubtitle">We've sent an email to {{resetPasswordEmail}} with password reset instructions.</div>
            <div class="forgotpass-subtitle">If the email doesn't show up soon, check your spam folder. We sent it from <span>confirm@vindome.com</span>.</div>

            <v-flex>
                <v-btn class="submit resetpassword-submit" v-on:click="resetPassword">
                    <router-link to="/login" >RETURN TO LOGIN</router-link>
                </v-btn>
            </v-flex>
        </v-flex>
    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: "Forgotpassword",
        data() {
            return {
                resetPasswordEmail: "",
                resetPasswordModal: false,
                resetPasswordSuccess: {
                    visible: false,
                    text: 'Please check your inbox for further instructions!'
                },
                resetPasswordFail: {
                    visible: false,
                    text: 'Email not found - please check it again!'
                },
                rules: {
                    required: value => value != '' || 'Required.'
                },
                valid: true,
            }
        },
        methods: {
            resetPassword() {
                var resetPassThis = this;

                if (this.resetPasswordEmail) {
                    this.resetPasswordSuccess.visible = true;
                    // axios.post('http://devauth.vindome.net/api/ResetPassword', {
                    //     headers: {
                    //     'Access-Control-Allow-Origin': '*',
                    // },
                    //     email: resetPassThis.resetPasswordEmail
                    // }).then(function (response) {
                    //     resetPassThis.resetPasswrodSuccess.visible = true;
                    // }).catch(function (err) {
                    //     resetPassThis.resetPasswordFail.visible = true;
                    // });
                }
            }
        }
    }
</script>

<style scoped>
    .forgotpass-container {
        height: 100%;
        background: #fff;
    }
    .forgotpass-title {
        font-weight: bold;
        font-size: 60px;
        color: #000;
        margin: 20px 0;
    }
    .forgotpass-subtitle {
        margin: 35px 0;
        color: #959595;
    }
    input, button {
        border: none;
        outline: none;
        background: none;
        font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    }
    input[type="text"] {
        display: block;
        width: 100%;
        font-size: 16px;
        border-bottom: 2px solid #E0E0E0;
        height: 60px;
        margin-top: 20px;
        max-width: 450px;
    }
    .addaddress-result-container {
        max-width: 350px;
        margin: 0 auto !important;
    }
    .delete-address-container {
        padding-top: 15px;
    }
    .v-card__title {
        background: #014e7a;
        color: #FFFFFF;
        flex-direction: column;
    }
    .v-card__title span {
        margin: 0 auto;
    }
    .resetpassword-submit {
        margin-top: 50px;
        background: #024E7B !important;
        color: #FFF !important;
        text-transform: uppercase;
        border-radius: 5px;
        max-width: 450px;
        width: 100%;
        height: 85px !important;
        font-size: 21px;
        letter-spacing: 1px;
    }
    .forgotpass-form {
        max-width: 460px;
        padding-top: 150px;
        margin: 0 auto;
    }
    .resetpassword-submit a {
        color: #FFF;
        text-decoration: none;
    }
</style>